<template>
  <div id="addBankCard">
    <common-header title="添加银行卡"></common-header>
    <div class="addBankCard__title">请绑定持卡人本人的银行卡</div>
    <Cell title="持卡人" :type="'input'" v-model="accountName" placeholder="请输入姓名"/>
    <Cell title="银行名称" :value="activeBank.name" @click="isShowBank = true"/>
    <Cell title="卡号" :type="'number'" v-model="accountIdCode" placeholder="请输入银行卡号"/>
    <div class="addBankCard__code">
      <span class="code__text">验证码</span>
      <input class="code__input" v-model="code" type="text" placeholder="请输入收到的验证码">
      <span class="code__btn" :class="{'sended': sended}" @click="sendCode">{{btnText}}</span>
    </div>
    <div class="addBankCard__btn" :class="{'active': isFinish}" @click="AddCard">绑定银行卡</div>

    <div class="picker needsclick" v-show="isShowBank">
      <van-picker :columns="bankList" value-key="name" :show-toolbar="true" @cancel="onCancel" @confirm="onConfirm"/>
    </div>
  </div>  
</template>

<script>
  import Cell from '@/components/Cell/Cell';

  export default {
    data() {
      return {
        bankList: [],
        isShowBank: false,
        code: '',
        checkKey:'',
        accountName: '',  //账号姓名
        accountIdCode: '', //银行账号
        activeBank: {}, //选择的银行对象
        changeBank: {}, //当前滚动的银行对象
        sended: false,
        btnText: '获取验证码',
      }
    },

    created() {
      this.getBankList();
    },

    computed: {
      isFinish() {
        if(this.accountName != '' && this.accountIdCode != '' && this.code != '' && Object.keys(this.activeBank).length > 0) {
          return true;
        }else {
          return false;
        }
      }
    },

    methods: {
      //获取银行卡列表
      getBankList() {
        this.$Api.getBankList(3).then((res) => {
          console.log(res)
          if(res.q.s == 0) {
            this.bankList = res.q.banks;
          }
        })
      },

      // //选择银行列表
      // onChange(picker,values) {
      //   this.changeBank = picker.getValues()[0];
      // },

      //取消显示银行列表
      onCancel() {
        this.isShowBank = false;
      },

      //保存银行选择的值
      onConfirm(value, index) {
        this.activeBank = value;
        this.isShowBank = false;
      },

      sendCode() {
        if(this.sended) {
          return false;
        }
        
        this.sended = true;
        let count = 60;
        for(let i = count; i >= 0; i--) {
          setTimeout(() => {
            this.btnText = i + 's';
            if(i == 0) {
              this.btnText = '获取验证码'
              this.sended = false;
            }
          }, (count - i - 1) * 1000)
        }
        
        
        let UserDetails = JSON.parse( localStorage.getItem('UserDetails') )
        
        let param = {
          a: 1,
          type: 7,
          mobile: UserDetails.mobile,
        }
				this.$Api.SMSCode(param).then((res)=>{
					if(res.q.s == 0){
						this.checkKey = res.q.checkKey
					}
				})
      },
			
			addBankCard(){
				let param = { 
          id: 0, 
          accountIdCode: this.accountIdCode, 
          accountName: this.accountName, 
          accountBankId: this.activeBank.id, 
        }
        
        this.$Api.AddCard(param).then((res) => {
          console.log(res)
          if(res.q.s == 0){
          	this.$router.replace({name:'WithDraw'})
          }
        })
			},
			
			
      //添加银行卡
      AddCard() {
        if(!this.isFinish) {
          return false;
        }
        //验证短信码
				let UserDetails = JSON.parse( localStorage.getItem('UserDetails') )
        let param = {
          a: 2,
          type: 7,
          mobile: UserDetails.mobile,
          checkKey:this.checkKey,
          code:this.code
        }
				this.$Api.SMSCode(param).then((res)=>{
					if(res.q.s == 0){
						this.addBankCard()
					}
				})
        
      }
    },

    components: {
      Cell
    }
  }
</script>

<style lang="scss">
  #addBankCard {
    .addBankCard__title {
      padding: 0 0.4rem;
      height: 1.2rem;
      font-size: 0.4267rem;
      line-height: 1.2rem;
      border-bottom: 1px solid #eee;
    }

    .addBankCard__code {
      /*display: flex;*/
      /*justify-content: space-between;*/
      
      height: 1.227rem;
      font-size: 0.4267rem;
      line-height: 1.227rem;
      border-bottom: 1px solid #eee;
      border-top: 1px solid #eee;

      .code__text {
      	display: inline-block;
        padding-left: 0.4rem;
        width: 2.0rem;
      }

      .code__input {
        text-align: right;
        width: 4.53rem;
      }

      .code__btn  {
        display: inline-block;
        float: right;
        text-align: center;
        width: 3.2rem;
        color: #fff;
        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);

        &.sended {
          background: #ccc;
        }
      }
    }

    .addBankCard__btn {
      margin: 0.667rem 0.4rem;
      width: calc(100% - 0.8rem);
      height: 1.2rem;
      line-height: 1.1733rem;
      font-size: 0.493rem;
      text-align: center;
      color: #fff;
      border-radius: 1.2rem;
      background: #ccc;

      &.active {
        background: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }

    .picker {
      position: fixed;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      background: rgba(0,0,0, 0.3);

      .van-picker {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
      }
    }
  }
</style>




